@mixin notebook {
	// kontener ma byc responsywny jesli width ekranu spadnie ponizej 1352px
	@media (max-width: 1352px) {
		@content;
		// content zeby moc wstawic wlasciwosci css do media
	}
}

@mixin mobile {
	@media (max-width: 792px) {
		@content;
	}
}

@mixin tablet {
	@media (max-width: 1024px) {
		@content;
	}
}

